<template>
  <div class="contanier">
    <div class="row" style="justify-content: center;" >
      <va-card :title="(showLabel? ($t('tables.labelChooseCraitaria')+' - '+filterTitle ) :'') " class="flex md12 sm12 xs12">
        <form @submit.prevent="okCreatefun()">
          <div class="row flex-center">
            <div class="flex md4 sm12 xs12 px-1" v-if="showDates">
              <label class="input-label"> {{$t('forms.dateTimePicker.from')}} </label>

              <va-date-picker
                v-model="searchObject.FromDate"
                :config="{maxDate: searchObject.ToDate}"

              />
            </div>
            <div class="flex md4 sm12 xs12 px-1" v-if="showDates">
              <label class="input-label"> {{$t('forms.dateTimePicker.to')}} </label>

              <va-date-picker
                v-model="searchObject.ToDate"
                :config="{minDate: searchObject.FromDate}"

              />
            </div>
            <div class="flex md4 sm12 xs12 px-1" v-for="(item, i) in data" :key="i">
              <label class="input-label" v-if="item.showLabel"> {{item.title}} </label>
              <!-- <div class="va-input" v-if="item.isString">
                <div class="va-input__container"
                  :style="{backgroundColor:'#f5f8f9',borderColor:$themes.gray}"  >
                  <div class="va-input__container__content-wrapper"
                    :style="{ paddingTop:'0'}">
                    <input
                      class="va-input__container__input"
                      :style="{ paddingBottom: '0.875rem' }"
                      type="text"
                      :placeholder="item.title"
                      v-model="searchObject[item.name]"
                    />
                  </div>
                </div>
              </div>
              <div class="va-input" v-else-if="item.isNum">
                <div class="va-input__container"
                  :style="{backgroundColor:'#f5f8f9',borderColor:$themes.gray}"  >
                  <div class="va-input__container__content-wrapper"
                    :style="{ paddingTop:'0'}">
                    <input
                      class="va-input__container__input"
                      :style="{ paddingBottom: '0.875rem' }"
                      type="number"
                      :min="item.min" :max='item.max' :step="item.step"
                      :placeholder="item.title"
                      v-model="searchObject[item.name]"
                    />
                  </div>
                </div>
              </div> -->
              <va-input v-model="searchObject[item.name]" v-if="item.isString"
                :placeholder="item.title"  @change="uuu()"/>
              <va-input v-model="searchObject[item.name]" v-else-if="item.isNum"
                type="number" :min="item.min" :max='item.max' :step="item.step"
                :placeholder="item.title" @change="uuu()"/>
              <va-checkbox
                v-else-if="item.isBoolean"
                :label="item.title"
                v-model="searchObject[item.name]"
                @change="uuu()"
              />
              <va-date-picker v-else-if="item.isDate"
                :class="item.class"
                v-model="searchObject[item.name]"
                :config="item.config"
                @change="uuu()"
              />
            </div>
            <div class="flex md4 sm12 xs12 px-1" v-if="showfkCompanyActivity || reqfkCompanyActivity">
              <label class="input-label"> {{$t('placeholder.CompanyActivity')}} </label>
              <va-select-c
                v-model="searchObject.Fk_Company_Activity"
                searchable
                textBy="name"
                keyBy="id"
                :options="fkCompanyActivity.options"
              />
            </div>
            <div class="flex md4 sm12 xs12 px-1" v-if="showfkCompany">
              <label class="input-label"> {{$t('placeholder.Company')}} </label>
              <va-select-c
                v-model="searchObject.FK_Company"
                searchable
                textBy="name"
                keyBy="id"
                :options="fkCompany.options"
              />
            </div>
            <div class="flex md4 sm12 xs12 px-1" v-if="showfkProductsGroups">
              <label class="input-label"> {{$t('placeholder.ProductsGroups')}} </label>
              <va-select-c
                v-model="searchObject.FK_Products_Groups"
                searchable
                textBy="name"
                keyBy="id"
                :options="fkProductsGroups.options"
              />
            </div>
            <div class="flex md4 sm12 xs12 px-1" v-if="showfkDeafultStock">
              <label class="input-label"> {{$t('placeholder.StockName')}} </label>
              <va-select-c
                v-model="searchObject.FK_Stock"
                searchable
                textBy="name"
                keyBy="id"
                :options="fkStock.options"
              />
            </div>
            <div class="flex md4 sm12 xs12 px-1" v-if="showfkTaxGroup">
              <label class="input-label"> {{$t('placeholder.TaxGroup')}} </label>
              <va-select-c
                v-model="searchObject.FK_TaxGroup"
                searchable
                textBy="name"
                keyBy="id"
                :options="fkTaxGroup.options"
              />
            </div>
            <div class="flex md4 sm12 xs12 px-1" v-if="showfkEmployees || reqfkEmployees">
              <label class="input-label"> {{$t('placeholder.empName')}} </label>
              <va-select-c
                v-model="searchObject.FK_Employees"
                searchable
                textBy="name"
                keyBy="id"
                :options="fKemp.options"
              />
            </div>
          </div>

          <div class="d-flex justify--center mt-3">
            <va-button type="submit" class="my-0">{{ $t('modal.ok') }} </va-button>
          </div>
        </form>
      </va-card>
    </div>
  </div>
</template>

<script>
import CityService from '../../services/BasicData/CityService'
import CompanyActivityService from '../../services/BasicData/CompanyActivityService'
import employeesService from '../../services/BasicData/employeesService'
import CompanyService from '../../services/BasicData/CompanyService'
import ProductsGroupsService from '../../services/BasicData/ProductsGroupsService'
import TaxGroupService from '../../services/BasicData/TaxGroupService'
import StockService from '../../services/BasicData/StockService'
import customerslistService from '../../services/customers/customerslistService'
import bus from '../../app/eventBus'
import helper from '../../helper/helper'
export default {
  name: 'TopFilter',
  props: {
    filterTitle: {
      type: String,
      default: '',
    },
    showLabel: {
      type: Boolean,
      default: true,
    },
    showDates: {
      type: Boolean,
      default: false,
    },
    showfkTaxGroup: {
      type: Boolean,
      default: false,
    },
    showfkEmployees: {
      type: Boolean,
      default: false,
    },
    reqfkEmployees: {
      type: Boolean,
      default: false,
    },
    showfkProductsGroups: {
      type: Boolean,
      default: false,
    },
    showfkCompanyActivity: {
      type: Boolean,
      default: false,
    },
    showfkCompany: {
      type: Boolean,
      default: false,
    },
    reqfkCompanyActivity: {
      type: Boolean,
      default: false,
    },
    showfkDeafultStock: {
      type: Boolean,
      default: false,
    },
    listOfStringsAndNumbers: {
      type: Array,
      default: () => [],
    },
  },
  data () {
    return {
      fkStock: {
        options: [],
      },
      fkCompanyActivity: {
        options: [],
      },
      fkCompany: {
        options: [],
      },
      fkProductsGroups: {
        options: [],
      },
      fkTaxGroup: {
        options: [],
      },
      fKCustomer: {
        valueName: '',
        valuePhone: '',
        options: [],
      },
      fKemp: {
        valueName: '',
        valuePhone: '',
        options: [],
      },
      searchObject: {
        FK_TaxGroup: '',
        FK_Stock: '',
        FK_Products_Groups: '',
        Fk_Company_Activity: '',
        FK_Company: '',
        FK_Employees: '',
      },
      data: [],
    }
  },
  created () {
    this.listOfStringsAndNumbers.forEach(e => {
      if (e.defaultValue) {
        this.searchObject[e.name] = e.defaultValue
      } else {
        if (e.isString) {
          this.searchObject[e.name] = null
        } else if (e.isNum) {
          this.searchObject[e.name] = null
        } else if (e.isBoolean) {
          this.searchObject[e.name] = null
        } else if (e.isDate) {
          if (e.curentTime) {
            this.searchObject[e.name] = helper.currentDateTime()
          } else {
            this.searchObject[e.name] = null
          }
        }
      }
    })
    this.data = Object.assign({}, this.listOfStringsAndNumbers)

    if (this.showfkCompanyActivity || this.reqfkCompanyActivity) {
      this.getComActivity()
    }
    if (this.showfkCompany) {
      this.getCompany()
    }
    if (this.showfkDeafultStock) {
      this.getStock()
    }
    if (this.showfkProductsGroups) {
      this.getProductsGroups()
    }
    if (this.showfkTaxGroup) {
      this.getTaxGroup()
    }
    if (this.showfkEmployees || this.reqfkEmployees) {
      this.getEmployee()
    }
  },
  methods: {
    uuu () {
      this.$forceUpdate()
    },
    getCustomer () {
      const params = {
        Customer_type: this.$store.state.consts.fK_Customer_Type_customer,
      }
      customerslistService.getAll(params)
        .then(response => {
          this.fKCustomer.options = response.data
        },
        ).catch(err => {
          if (err.response.status === 401) {
            this.showToast(this.$t('generic.errorYouDonotHaveAccess'), {
              icon: 'fa-exclamation',
              position: 'bottom-center',
              duration: 2500,
              type: 'error',
            })
          }
          console.error(err)
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    getEmployee () {
      employeesService.getAll()
        .then(response => {
          this.fKemp.options = response.data
        },
        ).catch(err => {
          if (err.response.status === 401) {
            this.showToast(this.$t('generic.errorYouDonotHaveAccess'), {
              icon: 'fa-exclamation',
              position: 'bottom-center',
              duration: 2500,
              type: 'error',
            })
          }
          console.error(err)
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    getComActivity () {
      CompanyActivityService.getAll()
        .then(response => {
          this.fkCompanyActivity.options = response.data
        },
        ).catch(err => {
          if (err.response.status === 401) {
            this.showToast(this.$t('generic.errorYouDonotHaveAccess'), {
              icon: 'fa-exclamation',
              position: 'bottom-center',
              duration: 2500,
              type: 'error',
            })
          }
          console.error(err)
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    getCompany () {
      CompanyService.getAll()
        .then(response => {
          this.fkCompany.options = response.data
        },
        ).catch(err => {
          if (err.response.status === 401) {
            this.showToast(this.$t('generic.errorYouDonotHaveAccess'), {
              icon: 'fa-exclamation',
              position: 'bottom-center',
              duration: 2500,
              type: 'error',
            })
          }
          console.error(err)
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    getStock () {
      StockService.getAll()
        .then(response => {
          this.fkStock.options = response.data
        },
        ).catch(err => {
          if (err.response.status === 401) {
            this.showToast(this.$t('generic.errorYouDonotHaveAccess'), {
              icon: 'fa-exclamation',
              position: 'bottom-center',
              duration: 2500,
              type: 'error',
            })
          }
          console.error(err)
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    getProductsGroups () {
      ProductsGroupsService.getAll()
        .then(response => {
          this.fkProductsGroups.options = response.data
        },
        ).catch(err => {
          if (err.response.status === 401) {
            this.showToast(this.$t('generic.errorYouDonotHaveAccess'), {
              icon: 'fa-exclamation',
              position: 'bottom-center',
              duration: 2500,
              type: 'error',
            })
          }
          console.error(err)
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    getTaxGroup () {
      TaxGroupService.getAll()
        .then(response => {
          this.fkTaxGroup.options = response.data
        },
        ).catch(err => {
          if (err.response.status === 401) {
            this.showToast(this.$t('generic.errorYouDonotHaveAccess'), {
              icon: 'fa-exclamation',
              position: 'bottom-center',
              duration: 2500,
              type: 'error',
            })
          }
          console.error(err)
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    getcity () {
      CityService.getAll()
        .then(response => {
          this.fKCity.options = response.data
        },
        ).catch(err => {
          if (err.response.status === 401) {
            this.showToast(this.$t('generic.errorYouDonotHaveAccess'), {
              icon: 'fa-exclamation',
              position: 'bottom-center',
              duration: 2500,
              type: 'error',
            })
          }
          console.error(err)
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    async okCreatefun () {
      if (this.reqfkEmployees || this.reqfkCompanyActivity) {
        if (this.reqfkEmployees && (this.searchObject.FK_Employees === '' || this.searchObject.FK_Employees === null || this.searchObject.FK_Employees === undefined)) {
          return this.showToast(this.$t('generic.errorChooseEmployee'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        } else if (this.reqfkCompanyActivity && (this.searchObject.Fk_Company_Activity === '' || this.searchObject.Fk_Company_Activity === null || this.searchObject.Fk_Company_Activity === undefined)) {
          return this.showToast(this.$t('generic.errorChooseCompanyActivity'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        }
      }
      var opjectBeforeEdit = Object.assign({}, this.searchObject)
      this.$root.$children[0].$children[0].$children[0].$children[0].minimaizeSidebarState(true)
      bus.$emit('SearchStart', opjectBeforeEdit)
    },
  },
}

</script>

<style lang="scss">
  .row-equal .flex {
    .va-card {
      height: 100%;
    }
  }

  .dashboard {
    .va-card {
      margin-bottom: 0 !important;
    }
  }

</style>
