import http from '../../http-common'

class CompanyService {
  getAll () {
    return http.get('Company')
  }

  getAllPagnation (params) {
    return http.get('Company', { params })
  }

  get (id) {
    return http.get(`Company/getById?Id=${id}`)
  }

  search (params) {
    return http.get('Company/Search', { params })
  }

  create (data) {
    return http.post('Company', data)
  }

  update (id, data) {
    return http.put(`Company?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`Company?Id=${id}`)
  }
}

export default new CompanyService()
