  methods: {
    validateEmail (d = '') {
      if (d === null || d === undefined || d === '') {
        return false
      }
      var emails = d.split(',')
      if (emails.length >= 1) {
        var eerror = false
        emails.forEach(email => {
          eerror = helper.validateEmail(email)
        })
        return eerror
      } else {
        return true
      }
    },
    validateLocaion (d = '') {
      if (d === null || d === undefined || d === '') {
        return false
      }
      var LongLat = d.split(',')
      if (LongLat.length === 2) {
        return !(this.isLatitude(LongLat[0]) && this.isLongitude(LongLat[1]))
      } else {
        return true
      }
    },
    isLatitude (lat) {
      return isFinite(lat) && Math.abs(lat) <= 90
    },

    isLongitude (lng) {
      return isFinite(lng) && Math.abs(lng) <= 180
    },
